import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModuleConstant } from 'src/app/shared/models/constants';
import { PageMasterModel } from 'src/app/shared/models/model';
import { PageMasterService } from 'src/app/shared/services/page-master.service';
import { ToastrService } from 'src/app/shared/services/toastr-message.service';

@Component({
  selector: 'app-page-master',
  templateUrl: './page-master.component.html'
})
export class PageMasterComponent implements OnInit {
  pageMasterForm!: FormGroup;
  pageMasters: PageMasterModel[] = [];
  modules = ModuleConstant;
  display: boolean = false

  constructor(private fb: FormBuilder, private pageMasterService: PageMasterService, private toastrService: ToastrService) {
    this.pageMasterForm = this.fb.group({
      id: ['0'],
      pageName: ['', Validators.required],
      isActive: true,
      menuOrder: ['0'],
      module: ['', Validators.required],
      icon: ['', Validators.required],
      link: ['', Validators.required],
    });
  }

  ngOnInit() {
    this.getPageMaster();
  }

  getPageMaster() {
    this.pageMasterService.get().subscribe((response: any) => {
      this.pageMasters = response;
    });
  }

  showDialog() {
    this.display = true;
    this.pageMasterForm.reset();
  }

  updateData() {
    let temp = [...this.pageMasters];
    temp = JSON.parse(JSON.stringify(temp));
    this.pageMasters = temp;
  }

  add() {
    const data = this.pageMasterForm.value;
    if (!data.id) {
      delete data.id;
    }

    this.pageMasterService.create(data).subscribe({
      next: (response: any) => {
        if (!!response.id) {
          this.pageMasters.push(response);
          this.updateData();
          this.toastrService.success('Menu Create Successfully.');
          this.display = false;
        }
      },
      error: () => {
        this.toastrService.error('Menu Create Failed.');
      }
    });
  }

  edit() {
    this.pageMasterService.update(this.pageMasterForm.value).subscribe((response: any) => {
      const data = this.pageMasterForm.value;
      const index = this.pageMasters.findIndex(i => i.id === data.id);
      this.pageMasters[index] = data;
      this.updateData();
      this.toastrService.success('Menu Updated Successfully.');
      this.display = false;
    });
  }

  onSubmit() {
    if (this.pageMasterForm.invalid) {
      return;
    }

    if (!this.pageMasterForm.value.id) {
      this.add();
    } else {
      this.edit();
    }
  }

  fillForm(pageMaster: PageMasterModel) {
    this.display = true;
    this.pageMasterForm.patchValue(pageMaster);
  }

  deleteMenu(id: number) {
    this.pageMasterService.delete(id).subscribe((response: any) => {
      const index = this.pageMasters.findIndex(i => i.id == id);
      this.pageMasters.splice(index, 1);
      this.updateData();
      this.toastrService.success('Menu Deleted Successfully.');
    });
  }

  public close(): void {
    window.alert('Are u Sure Leave From');
    this.display = false;
  }

}
